import back from '@/components/back'
export default {
	components:{
        back
    },
	data() {
		return {
			info: null,
			medList: [],
			flag: false,
			show:false,
			toxicityList:[],
			toxicityInfo:"",
			user: this.$store.state.userInfo.Data,
			showConfirm: false,
			DrType:0,
			RejectReasonIds: [],
			reason: [],
		}
	},
	created() {
		this.pull()
		this.pullReject()
	},
	mounted: function() {
		document.title = this.$route.meta.title
	},
	methods: {
		pull() {
			this.id = this.$route.query.id;
			this.$post('/Prescription/Prescription/GetTCMPrescription', {
				id: this.id,
			}).then(o => {
				this.info = o
				this.RejectReasonIds = o.RejectReasonId ? o.RejectReasonId.split('，') : []
				if(this.info.ListDetails && this.info.ListDetails.length){
					this.info.ListDetails.forEach((v,k) => {
						if(v.Toxicity == 1){
							//该药品含有毒性
							this.toxicityList.push(v.Name)
						}
					})
					if(this.toxicityList && this.toxicityList.length){
						this.toxicityInfo = this.toxicityList.join("、")
						this.show = true
					}
				}
				this.$get('PlatFormAPI/Doctor/QueryDoctorInfoByDrId',{
				    DrId: this.user.User.DoctorId
				}).then(o => {
					 this.DrType = o.DrType
				})
			})
		},
		pullReject(){
        	//中药驳回的理由
            this.$get('PlatFormAPI/Category/QueryItemByCategoryId',{
                CategoryId: 57
            }).then(o => {
                this.reason = o
            })
        },
        toReset(){
            /*this.$store.dispatch('pushDiagnoses2', null)*/
            this.$store.dispatch('pushpatient',{
                PatientName: this.info.PatientName,
                PatientSex: this.info.PatientSex,
                PatientId: this.info.PatientId,
                Age: this.info.Age,
            })
            this.$store.dispatch('pushpInfo',{
                ApplicantName: this.info.ApplicantName,
                ApplicantId: this.info.ApplicantId,
                PatientInfo:{
                    PatientName: this.info.PatientName,
                    PatientSex: this.info.PatientSex,
                    PatientId: this.info.PatientId,
                    Age: this.info.Age,
                }
            })
			this.$store.dispatch('pushchList', [])
			this.$store.dispatch('pushisUsePre',false)
            this.$router.push('/chooseDrugs?cid=1')
        },
        modify(){
           // this.$store.dispatch('pushpullFlag', 0)
            this.$store.dispatch('pushpatient',{
                PatientName: this.info.PatientName,
                PatientSex: this.info.PatientSex,
                PatientId: this.info.PatientId,
                Age: this.info.Age,
            })
            this.$store.dispatch('pushpInfo',{
                ApplicantName: this.info.ApplicantName,
                ApplicantId: this.info.ApplicantId,
                PatientInfo:{
                    PatientName: this.info.PatientName,
                    PatientSex: this.info.PatientSex,
                    PatientId: this.info.PatientId,
                    Age: this.info.Age,
                }
            })
            this.$router.push(`/cM/prescription?DataSource=2&DataId=${this.info.Id}&subId=${this.info.MedicationForm}`)
        },
        reject(){
            this.$bridge.callhandler('preReject')
        },
        accept(){
            this.$bridge.callhandler('preAccept')
        },
		withdraw(){
            this.$post('Prescription/Prescription/RevokePrescription',{
            	PrescriptionType:2,//处方类型 1-西药处方,2-中药处方
                PrescriptionId: this.$route.query.id,
                DoctorId: this.user.User.DoctorId
            }).then(o => {
                this.$toast('撤回成功')
                this.pull()
            })
        },
	}
}



